@import url(https://fonts.googleapis.com/css?family=Roboto&display=swap);
.app {
  text-align: center;
  font-family: Roboto;
}

.header {
  background-color: #00c5c0;
  background-color: var(--color-green, #00c5c0);
  color: #fff;
  color: var(--color-white, #fff);
  height: 4rem;
  width: 100%;
  font-size: 2rem;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  text-align: left;
  padding-left: 1.5rem;
}

.container {
  display: flex;
}

.sidebar {
  background-color: #e0e0e0;
  background-color: var(--color-grey, #e0e0e0);
  height: calc(100vh - 4rem);
  width: 25%;
  min-width: 12rem;
}

@media screen and (max-width: 48rem) {
  .container {
    flex-wrap: wrap;
  }
  .sidebar {
    width: 100%;
    height: 100%;
  }
}

.main {
  height: calc(100vh - 4rem);
  width: 100%;
}

.timer {
  text-align: right;
  height: 3rem;
  line-height: 3rem;
  padding-right: 1rem;
}

@media screen and (max-width: 48rem) {
  .canvas {
    max-width: 100%;
    max-height: calc(100% - 3rem);
  }
}
